<template>
    <div class="distributor-main" :class="{showSearch:showSearch}">
        <div class="search-box">
            <el-input :placeholder="$L('输入用户名称/姓名')" size="small" v-model="inputValue" class="input-with-select"
                      clearable
                      @keyup.enter.native="$event.target.blur"
                      @change="searchFunc">
                <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>
            <div class="search-close" @click="doSearch(false)"><i class="el-icon-close"></i></div>
        </div>
        <vue-scroll>
            <div class="main-table member"  v-if="tableData && tableData.hasOwnProperty('list') && tableData.list.length>0">
                <div v-for="item in tableData.list" class="table-box">
                    <div class="l-box">
                        <user-avatars :src="item.headPic" class="img"></user-avatars>
                        <p class="p">{{item.userName}}</p>
                    </div>
                    <div class="r-box">
                        <p class="p">
                            <span class="span">{{$L('姓名')}}:</span>
                            <span class="name">{{ item.contact ? item.contact : $L('无') }}</span>
                        </p>
                        <p class="p">
                            <span class="span">{{$L('手机号')}}:</span>{{ item.mobile ? item.mobile : $L('无') }}
                        </p>
						<p class="p">
						    <span class="span">{{$L('邮箱')}}:</span>{{ item.email ? item.email : $L('无') }}
						</p>
                        <p class="p">
                            <span class="span">{{$L('注册时间')}}:</span>{{ item.addTime ? item.addTime : $L('无') }}
                        </p>
                    </div>
                    <router-link :to="{name:'distributorUserOrderMobile',params:{id:item.id,type:'all'}}">
                        <span class="order mainFontColor mainBorderColor">{{$L('查看分销订单')}}</span>
                    </router-link>
                </div>
                <!-- 分页 -->
                <div class="list-pages" v-if="tableData.allNum > pageSize">
                    <el-pagination
                            background
                            small
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-size="pageSize"
                            layout="prev, pager, next"
                            :total="tableData.allNum">
                    </el-pagination>
                </div>
            </div>
            <div class="no-data" v-else>{{$L('暂无数据')}}</div>
        </vue-scroll>
    </div>
</template>

<script>
    import UserAvatars from "../../../components/common/userAvatars";
    export default {
        name: "distributorList",
        components: {UserAvatars},
        props : {

        },
        data () {
            return {
                showSearch: false,
				tableData:{},
				inputValue:'',
				pageSize:10,
				currentPage:1,
            }
        },
        computed:{
            mobileTopBtn: {
                get() {
                    return this.$store.state.mobileTopBtn;
                },
                set(val) {
                    this.$store.commit('setMobileTopBtn', val);
                }
            }
        },
		created() {
			this.makeTop();
			this.getData();
		},
		beforeRouteUpdate(to, from, next) {
		    // 在当前路由改变，但是该组件被复用时调用
		    // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
		    // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
		    // 可以访问组件实例 `this`
		    this.makeTop();
		    next();
		},
		beforeDestroy() {
		    //销毁当前页面时，必须清空顶部按钮，不然会影响其他页面
		    this.mobileTopBtn = null
		},
        methods:{
			getData() {
			    const _self = this;
			    _self.loading = true;
			    let arr = {
			        p:_self.currentPage,
			        keyword:_self.inputValue?_self.inputValue:"",
			    };
			    _self.$api('getMembersList', arr, function (reData) {
			        if (reData.flag === 1) {
			            _self.tableData = reData.data;
			            console.log(_self.tableData);
			            _self.loading = false;
			        } else if (reData.flag === -1) {
			            //登录超时了
			            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
			                confirmButtonText: _self.$L('重新登录'),
			                cancelButtonText: _self.$L('取消'),
			                type: 'error',
			                distinguishCancelAndClose: true
			            }).then(() => {
			                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
			            }).catch(action => {
                            _self.$router.push({name: 'distributorMain'});
			            });
			        } else {
			            //未知错误
			            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
			                confirmButtonText: _self.$L('点击重试'),
			                cancelButtonText: _self.$L('返回首页'),
			                type: 'error',
			                distinguishCancelAndClose: true
			            }).then(() => {
			                _self.getData();
			            }).catch(action => {
			                _self.$gotoWebHome();
                            _self.$router.push({name: 'distributorMain'});
			            });
			        }
			    }, function (error) {
			        //网络请求错误
			        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
			            confirmButtonText: _self.$L('点击重试'),
			            cancelButtonText: _self.$L('返回首页'),
			            type: 'error',
			            distinguishCancelAndClose: true
			        }).then(() => {
			            _self.getData();
			        }).catch(action => {
                        _self.$router.push({name: 'distributorMain'});
			        });
			    });
			},
            currentChange(val){
                this.$emit('action',{
                    action:'page',
                    value:val
                });
            },
			searchFunc(val){
			    this.inputValue = val;
			    this.getData();
			},
			handleCurrentChange(val){
			    this.currentPage = val;
			    this.getData();
			},
            makeTop(){
				this.mobileTopBtn = [
					{
						icon: 'bbx-icon-sousuo',
						func: () => {
							this.doSearch(true);
						}
					},
				];
            },
            doSearch(type){
                if(type){
                    this.showSearch = true;
                }else{
                    this.searchType = "all";
                    this.showSearch = false;
                }
            }
        },
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/lib.scss";
    .distributor-main{
        .search-box{
            position: absolute;
            width: 100%;
            height: 45px;
            line-height: 45px;
            background-color: #fff;
            top: -45px;
            left: 0;
            z-index: 100;
            border-bottom: 1px solid #E4E7ED;
            @include diy-animation-show(top);
            .input-with-select{
                width: 260px;
                margin-left: 10px;
            }
            .search-close{
                width: 45px;
                height: 45px;
                text-align: center;
                line-height: 45px;
                position: absolute;
                top: 0;
                right: 0;
                z-index: 100;
                font-size: 18px;
                color: #909399;
                &:active{
                    color: #606266;
                }
            }
        }
        .main-table{
            position: relative;
            z-index: 0;
            @include diy-animation-show(margin);



            .table-box{
                position: relative;
                overflow: hidden;
                padding: 20px 15px;
                background: #fff;
                height: 230px;
                margin-bottom: 10px;
                .l-box{
                    float: left;
                    width: 30%;
                    text-align: center;
                    margin-right: 10px;
                    .img{
                        width: 65px;
                        height: 65px;
                        border-radius: 50%;
                    }
                    .p{
                        line-height: 2;
                    }
                }
                .r-box{
                    float: left;
                    .p{
                        line-height: 2;
                        color: #606266;
                        .span{
                            display: inline-block;
                            width: 75px;
                            text-align: right;
                            padding-right: 5px;
                            color: #303133;
                        }
                        .name{
                            vertical-align: bottom;
                            display: inline-block;
                            width: 90px;
                            overflow: hidden;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                        }
                    }
                }
                .font-error {
                    color: #F56C6C;
                }
                .font-success {
                    color: #67C23A;
                }
                .font-warning {
                    color: #E6A23C;
                }
                .status{
                    position: absolute;
                    top: 17px;
                    right: 20px;
                }
                .order{
                    position: absolute;
                    right: 20px;
                    bottom: 20px;
                    display: inline-block;
                    width: 90px;
                    height: 30px;
                    line-height: 30px;
                    text-align: center;
                    border: 1px solid;
                    border-radius: 20px;
                }
            }

            &.member{
                .table-box{
                    height: 170px;
                }
            }

            .list-pages{
                text-align: center;
                padding: 10px 0;
            }
        }
        .no-data{
            position: relative;
            width: 100%;
            height: 170px;
            line-height: 170px;
            text-align: center;
            background-color: #ffffff;
        }
        &.showSearch{
            .search-box{
                top: 0;
            }
            .main-table{
                margin-top: 45px;
            }
        }
    }
</style>